  border-left: 5px solid #009ca9;
}
.hero-section .breadcrumbs {
  opacity: 0.75;
  margin-bottom: 1.3125rem;
  margin-top: 2.125rem;
}
@media (min-width: 992px) {
  .hero-section .breadcrumbs {
    margin-bottom: 1.8rem;
    margin-top: 2.6rem;
  }
}
.hero-section:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
}

.home .hero-section {
  min-height: 30rem;
}
@media (max-width: 991.98px) {
  .home .hero-section {
    padding-top: 2rem;
  }
}

.overlay-dark {
  background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 95%);
